            <p>Web Plattformen und interne Web-Applikationen mssen hohen Anforderungen gengen: Plattformbergreifend auf Desktop und Mobile nutzbar sein, schnelle Ladezeiten gewhrleisten und dem Unternehmen erlauben, Inhalte ber ein CMS selbstndig anzupassen.</p>
