    </div>
  </curved>
</template>

<script>
import Curved from './Curved.vue'
import CtaButton from './CtaButton.vue'

export default {
  name: 'Solutions',
  components: {
    Curved,
    CtaButton
  },
  data: () => {
    return {
      active: [],
      orientations: [4,4,4,4,4,4], // 1,2,3,4 => top,right,bottom,left
      isEffect: -1,
      afterEffect: -1
    }
  },
  methods: {
    onClickActive(id) {
      var i;
      if(!this.isActive(id)) {
        if(this.isDesktop()) {
          if(id >= 3) {
            for (i = 0; i < 3; i++) {
              if(this.isActive(i)) {
                this.disableActive(i);
              }
            }
          } else {
            for (i = 3; i < 6; i++) {
              if(this.isActive(i)) {
                this.disableActive(i);
              }
            }
          }
        } else {
          for (i = 0; i < 6; i++) {
            if(this.isActive(i)) {
              this.disableActive(i);
            }
          }
        }
        this.enableActive(id)
      } else {
        this.disableActive(id);
      }
    },
    disableActive(id) {
      var index = this.active.indexOf(id)
      this.active.splice(index, 1)
    },
    enableActive(id) {
      this.active.push(id)
    },
    isActive(id) {
      return this.active.indexOf(id) !== -1
    },
    onClickCta() {
      this.$emit('clicked', 'Potential nutzen')
    },
    disableEffect() {
      if(this.isEffect != -1) {
        setTimeout(() => {
          this.isEffect = -1;
        }, 400);
      }
    },
    left(id) {
      this.orientations[id-1] = 4;
    },
    right(id) {
      this.orientations[id-1] = 2;
    },
    top(id) {
      this.orientations[id-1] = 1;
    },
    bottom(id) {
      this.orientations[id-1] = 3;
    },
    isDesktop() {
      return window.innerWidth >= 1280;
    }
  }
}
</script>

<style scoped>
#services6,
#services6 h1,
#services6 h3 {
  color: #fff;
}
.gradient {
