  margin-bottom: 0;
  overflow: hidden;
  border: 1px solid transparent;
  border-bottom: none;
  margin-top: 0;
}
.active h3 {
  border-color: #fff;
}
h3 span {
  font-size: 1.0em;
  line-height: 1.0em;
  padding-left: 10px;
}
article p {
  border: 1px solid #fff;
  border-top: none;
  padding: 0 10px 10px 10px;
  display: none;
}
article.active p {
  display: block;
}
.effect {
  position: absolute;
  left: -100%;
  top: 0;
  background-color: #fff;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
article .effect.right {
  left: 100%;
}
article .effect.top {
  left: 0;
  top: -100%;
}
article .effect.bottom {
  left: 0;
  top: 100%;
}
article:hover .effect {
  left: 0;
  top: 0;
}
article.is-effect .effect {
  transition: all 0.4s ease 0s;
}
article .effect .text {
  position: relative;
  color: #5FCCC7;
  transition: all 0.4s ease 0s;
}
article .effect.left .text {
  left: 100%;
  top: 0;
}
article .effect.right .text {
  left: -100%;
  top: 0;
}
article .effect.top .text {
  left: 0;
  top: 100%;
}
article .effect.bottom .text {
  left: 0;
  top: -100%;
}
article:hover .effect .text {
  left: 0;
  top: 0;
}
.icon {
  float: right;
  margin: -40px 25px 0 0;
  width: 30px;
  height: 30px;
}
article.active .icon {
  transform: rotate(180deg);
}
.area {
  position: absolute;
}
  .area-y {
    top: -30px;
    width: 30px;
    height: calc(100% + 2 * 30px);
  }
  .area-left {
    left: -30px;
  }
  .area-center1 {
    left: calc((100% - 2 * 2rem) / 3);
    width: 30px;
  }
  .area-center2 {
    left: calc((100% - 2 * 2rem) / 3 * 2 + 2rem);
    width: 30px;
  }
  .area-right {
    right: -30px;
  }
  .area-x {
    left: -30px;
    width: calc(100% + 2 * 30px);
    height: 30px;
  }
  .area-top {
    top: -30px;
  }
  .area-middle {
    top: calc(50% - 30px / 2);
  }
  .area-bottom {
    bottom: -30px;
  }
</style>